import { fromJS } from 'immutable';
import * as actionTypes from './actionTypes';
const selectData = {
    'key': 'Y',
    'keyText': '',
    'showId':'',
    'arrData': [
        { 'key': 'Y','text': '' },
        { 'key': 'N','text': '' }
    ] };


/***********   ****************/
const defaultState = fromJS({  //  store store
    pending: false,
    selectData ,
    exceptionData: [], //getExceptionData: [] 
    exceptionTableData:[],
});

export default (state = defaultState, action) => {
    if(action.type === actionTypes.INIT_EXCEPTION_LIST_ACTION) {
        const newState = state.merge({ exceptionData: action.exceptionData });
        return  newState ;
    }

    if(action.type === actionTypes.SELECT_CHANGE_ACTION) {
        // console.log(25);
        const newState = state.merge({ exceptionTableData: action.exceptionTableData});      console.log(state);
        return newState ;
    }
    return state;
};
